@import url("https://fonts.googleapis.com/css?family=Inconsolata:400,700&subset=latin-ext");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-family: 'Inconsolata', monospace;
  font-size: 20px;
  color: #d2dae2; }

body {
  overflow: hidden; }

.container {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  background-color: #f3f3f3; }

.container--inner {
  border: 15px #d2dae2 solid;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  width: 95%;
  height: 90vh;
  margin: 0 auto;
  max-width: 1480px;
  background-color: transparent;
  z-index: 4; }
  .container--inner::before, .container--inner::after {
    background-color: transparent;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: '';
    width: 92%;
    height: 96%;
    border: 1px solid #d2dae2;
    mix-blend-mode: color-dodge;
    z-index: 2;
    pointer-events: none; }
  .container--inner::after {
    width: 95%;
    height: 93%; }

.site-change {
  position: absolute;
  bottom: -100%;
  width: 100%;
  height: 100%;
  background-color: #d2dae2;
  z-index: 4; }

@media screen and (max-width: 760px) {
  .site-change {
    position: fixed;
    bottom: 100%;
    left: 0;
    z-index: 4; } }

.site-change--active {
  animation: anim 2s 1 both; }

@keyframes anim {
  0% {
    bottom: -110%; }
  50% {
    bottom: 0; }
  65% {
    bottom: 0; }
  100% {
    bottom: 110%; } }

.nav__ul {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all 0.6s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  list-style: none;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  color: #d2dae2;
  z-index: 5;
  opacity: 0;
  will-change: opacity, width;
  pointer-events: none;
  font-size: 1.3rem;
  font-weight: bold; }
  .nav__ul li {
    margin: 30px 0;
    text-transform: uppercase; }
    .nav__ul li a {
      text-decoration: none;
      color: grey; }
  .nav__ul li:hover a {
    transition: all 0.6s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    color: white;
    cursor: pointer; }

.nav:hover .nav__ul {
  opacity: 1;
  pointer-events: all;
  width: 20vw;
  position: absolute; }

.hamburger {
  font-size: 3vw;
  position: absolute;
  top: 5%;
  right: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 40px;
  height: 40px;
  z-index: 3;
  cursor: pointer; }
  .hamburger span {
    width: 100%;
    height: 5px;
    background-color: #1e272e; }

@media screen and (max-width: 760px) {
  .nav__ul {
    position: fixed;
    height: 100%;
    mix-blend-mode: normal; }
  .nav:hover .nav__ul {
    position: fixed;
    width: 85vw; } }

.home {
  position: relative;
  max-width: 1480px;
  margin: 0 auto;
  background-color: #2d3986;
  height: 100%;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden; }
  .home__h1 {
    font-size: 3rem;
    text-transform: uppercase;
    padding: 10px 40px; }
  .home__button {
    position: relative;
    z-index: 4;
    margin: 5% 0;
    color: #d2dae2;
    background-color: transparent;
    border: 1px solid #d2dae2;
    padding: 10px 15px;
    text-transform: uppercase;
    cursor: pointer; }
    .home__button::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 120%;
      height: 100%;
      background-color: #202960;
      transform: translate(-50%, -50%) rotate(-5deg);
      opacity: 0;
      transition: 0.3s ease-in-out;
      z-index: -1; }
    .home__button:hover::before {
      opacity: .7; }

@media screen and (max-width: 760px) {
  .home {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    border: none; }
    .home__h1 {
      font-size: 2rem; }
    .home__h2 {
      font-size: 1.2rem; } }

.about {
  position: relative;
  max-width: 1480px;
  padding: 100px 70px;
  overflow: hidden;
  background-color: #d2dae2;
  color: #2d3986;
  height: 100%;
  width: 100%;
  z-index: 1;
  display: grid;
  grid-gap: 30px;
  border: 15px #2d3986 solid;
  overflow-y: scroll;
  grid-template-columns: repeat(2, 1fr); }
  .about__h1 {
    font-size: 1.5rem;
    text-transform: uppercase;
    margin: 10px;
    grid-column: 1 / -1; }
  .about__basic-info {
    list-style: none;
    padding: 0 15px;
    font-weight: 700;
    color: #1e272e; }
    .about__basic-info li {
      padding: 20px 0;
      border-bottom: 1px solid #2d3986; }
      .about__basic-info li span {
        font-weight: normal;
        color: #2d3986; }
    .about__basic-info li:first-child {
      padding-top: 0; }
    .about__basic-info li:last-child {
      border-bottom: none; }
  .about__description__button {
    position: relative;
    z-index: 4;
    margin: 5% 0;
    color: #2d3986;
    background-color: transparent;
    border: 1px solid #2d3986;
    padding: 10px 15px;
    text-transform: uppercase;
    cursor: pointer; }
    .about__description__button::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 120%;
      height: 100%;
      background-color: #202960;
      transform: translate(-50%, -50%) rotate(-5deg);
      opacity: 0;
      transition: 0.3s ease-in-out;
      z-index: -1; }
    .about__description__button:hover::before {
      opacity: .7; }
  .about::-webkit-scrollbar {
    width: 5px; }
  .about::-webkit-scrollbar-thumb {
    background-color: #2d3986;
    outline: 1px solid #1e272e; }

@media screen and (max-width: 760px) {
  .about {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    border: none;
    grid-template-columns: 1fr; } }

.icons {
  grid-column: -1/1;
  display: grid;
  justify-content: center;
  align-content: center;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 10px; }
  .icons__icon {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center; }
    .icons__icon i {
      font-size: 2.4rem; }

.education {
  grid-column: -1/1;
  display: grid;
  justify-content: center;
  align-content: center;
  grid-template-columns: repeat(auto-fit, minmax(200px, 300px));
  grid-gap: 30px; }
  .education__item {
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    color: #2d3986;
    font-weight: 700;
    border: 1px solid #1e272e; }
    .education__item p:first-child {
      color: #1e272e; }
